	module.exports = {"data":{"challengeNode":{"title":"Use the flex-grow Property to Expand Items","description":["与<code>flex-shrink</code>相对的是<code>flex-grow</code>。你应该还记得，<code>flex-shrink</code>会在容器太小时对元素作出调整。相应地，<code>flex-grow</code>会在容器太大时对元素作出调整。","例子与上一个挑战相似，如果一个项目<code>flex-grow</code>属性的值为 1，另一个<code>flex-grow</code>为 3，那么 3 的会比 1 的扩大三倍。","<hr>","为<code>#box-1</code>和<code>#box-2</code>添加 CSS 属性<code>flex-grow</code>，<code>#box-1</code>的值设为 1，<code>#box-2</code>的值设为 2。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/css-flexbox/use-the-flex-grow-property-to-expand-items","blockName":"CSS Flexbox","tests":[{"text":"<code>#box-1</code>元素应有<code>flex-grow</code>属性，其值应为 1。","testString":"assert($('#box-1').css('flex-grow') == '1', '<code>#box-1</code>元素应有<code>flex-grow</code>属性，其值应为 1。');"},{"text":"<code>#box-2</code>元素应有<code>flex-grow</code>属性，其值应为 2。","testString":"assert($('#box-2').css('flex-grow') == '2', '<code>#box-2</code>元素应有<code>flex-grow</code>属性，其值应为 2。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style>\n  #box-container {\n    display: flex;\n    height: 500px;\n  }\n  \n  #box-1 {\n    background-color: dodgerblue;\n    height: 200px;\n    \n  }\n  \n  #box-2 {\n    background-color: orangered;\n    height: 200px;\n    \n  }\n</style>\n\n<div id=\"box-container\">\n  <div id=\"box-1\"></div>\n  <div id=\"box-2\"></div>\n</div>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/css-flexbox/use-the-flex-basis-property-to-set-the-initial-size-of-an-item","id":"587d78ae367417b2b2512afc"},"slug":"/responsive-web-design/css-flexbox/use-the-flex-grow-property-to-expand-items"}}

